import React, { FC, useState } from "react";
import ReactDOM from "react-dom";
import { Toast } from 'antd-mobile';
import "./index.less";

const Test: FC<any> = () => {
  const [square, setSquare] = useState({long: undefined, wide: undefined});
  const [circular, setCircular] = useState({
    diameter: undefined,
    thickness: undefined,
    long: undefined,
    roots: undefined,
  });
  const [results, setResults] = useState([]);
  const [total, setTotal] = useState<number>();
  const handleSquareInputChange = (type: string) => (e: any) => {
    const value = e.target.value;
    setSquare({ ...square, [type]: value });
  }
  const handleProductBtnClick = () => {
    try {
      const val = (parseFloat(square.long) + parseFloat(square.wide)) * 2 / 3.14;
      if (!val) {
        Toast.fail('');
        return;
      }
      setCircular({ ...circular, diameter: val.toFixed(2) });
    } catch (err) {
      Toast.fail(err);
    }
  }
  const handleCircularInputChange = (type: string) => (e: any) => {
    const value = e.target.value;
    setCircular({ ...circular, [type]: value });
  }
  const handleCalculation = () => {
    const { diameter, thickness, long, roots } = circular;
    if (diameter && thickness && long && roots) {
      try {
        const weight = (parseFloat(diameter) - parseFloat(thickness)) * thickness * 0.02466 * long * roots;
        if (weight) {
          setResults([...results, weight]);
        }
      } catch(err) {
        Toast.fail(err);
      }
    } else {
      Toast.fail('');
    }
  }
  const handleClear = () => {
    setSquare({ long: '', wide: '' });
    setCircular({ diameter: '', thickness: '', long: '', roots: '' });
  }
  const handleSum = () => {
    if (results.length) {
      try {
        const val = results.reduce((prev, curr) => parseFloat(prev) + parseFloat(curr), 0);
        setTotal(val);
      } catch (err) {
        Toast.fail(err);
      }
    }
  }
  return (
    <div>
      <article className="square">
        <h3></h3>
        <div className="square-content">
          <div className="square-item">
            <span>(mm)</span>
            <input type="text" value={square.long} placeholder="" onChange={handleSquareInputChange('long')} />
          </div>
          <div className="square-item">
            <span>(mm)</span>
            <input type="text" value={square.wide} placeholder="" onChange={handleSquareInputChange('wide')} />
          </div>
          <div className="btn" onClick={handleProductBtnClick}></div>
        </div>
      </article>
      <article className="circular">
        <h3></h3>
        <div className="circular-content">
          <div className="circular-item">
            <span>(mm)</span>
            <input type="text" value={circular.diameter} placeholder="" onChange={handleCircularInputChange('diameter')} />
          </div>
          <div className="circular-item">
            <span>(mm)</span>
            <input type="text" value={circular.thickness} placeholder="" onChange={handleCircularInputChange('thickness')} />
          </div>
          <div className="circular-item">
            <span>(m)</span>
            <input type="text" value={circular.long} placeholder="" onChange={handleCircularInputChange('long')} />
          </div>
          <div className="circular-item">
            <span></span>
            <input type="text" value={circular.roots} placeholder="" onChange={handleCircularInputChange('roots')} />
          </div>
        </div>
      </article>
      <div className="btns-warp">
        <div className="btn calculation-btn" onClick={handleCalculation}></div>
        <div className="btn" onClick={handleSum}></div>
        <div className="btn danger" onClick={handleClear}></div>
      </div>
      <div className="results">
        {
          results.length ? results.map((item, index) => {
            return (
              <div className="result-item" key={index}>{item?.toFixed(2)}</div>
            )
          }) : null
        }
      </div>
      <div className="total">{total?.toFixed(2)}</div>
    </div>
  );
};

export default ReactDOM.render(<Test />, document.querySelector("#root"));
